<template>
  <div id="my-Profile">
    <div class="header">Profile页面的内容 {{ username }}</div>
    <div class="main">
        {{$store.state.lists[0]}}
        <button @click="change" >change</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyProfile",
  data() {
    return {
    };
  },
  computed: {
  },
  props: ["username"],
  methods: {
      change() {
           this.$store.dispatch('aUpdateInfo')
           .then(res => {
               this.$store.commit('updateInfo',res.data)
           })
           
      }
          
  },
};
</script>

